import React from "react";

const CompCard = (props) => {
  return (
    <>
      <div className="compcard">
        <div class="card py-1 pl-2">
          <div class="card-body p-3">
            <div class="row">
              <div class="col-8">
                <div class="numbers">
                  <p class="text-sm mb-0 text-capitalize font-weight-bold">
                    {props.topic}
                  </p>
                  <h5 class="font-weight-bolder mb-0 pt-2">
                    {props.headtopic}
                    <span class="text-success text-sm font-weight-bolder">
                      <a href={props.link}>{props.text}</a>
                    </span>
                  </h5>
                </div>
              </div>
              <div class="col-4 text-end">
                <div class="icon icon-shape bg-gradient-primary shadow text-center border-radius-md">
                  <svg
                    class="css-1cw4hi4"
                    focusable="false"
                    viewBox="0 0 24 24"
                    aria-hidden="true"
                    data-testid="TableChartOutlinedIcon"
                  >
                    <path d="M20 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h15c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm0 2v3H5V5h15zm-5 14h-5v-9h5v9zM5 10h3v9H5v-9zm12 9v-9h3v9h-3z"></path>
                  </svg>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default CompCard;
