import React from "react";

const MuiCardSmall = (props) => {
  return (
    <>
      <div className="mui-small-card">
        <div className={"card " + props.classforcard}>
          <ul className="card-ul-small p-3">
            <li className="card-li-small">
              <div className="icon-box">
                <svg
                  class="css-1cw4hi4"
                  focusable="false"
                  viewBox="0 0 24 24"
                  aria-hidden="true"
                  data-testid="TableChartOutlinedIcon"
                >
                  <path d="M20 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h15c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm0 2v3H5V5h15zm-5 14h-5v-9h5v9zM5 10h3v9H5v-9zm12 9v-9h3v9h-3z"></path>
                </svg>
              </div>
              <div className="meta-div pl-3">
                <div className="count-box">
                  <p class="card-mui-title">{props.count}</p>
                </div>
                <div className="card-mui-small-topic">
                  <p className="card-outline">{props.topic}</p>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </>
  );
};

export default MuiCardSmall;
