import React from "react";
import CompCard from "../CompCard";
import "../ManageJob.css";

const CompetitionCardRoundList = (props) => {
  return (
    <>
      <div className="card">
        <div className="row">
          <div className="col-md-9">
            <div className="d-flex">
              <div className="comp-icon"></div>
              <h2 className="comp-title">{props.title}</h2>
            </div>
          </div>
          <div className="col-md-3 text-right">
            <a href={props.link} className="btn btn-outline-primary">
              Create &gt;
            </a>
          </div>
        </div>
      </div>
    </>
  );
};

const SectionCreateCompetition = () => {
  return (
    <>
      <section className="p-5" id="CreateCompetition">
        <div className="container" id="job-box">
          <div className="row px-5 py-3">
            <div className="col-md-12">
              <div class="job-bx submit-resume">
                <div class="job-bx-title clearfix">
                  <h5 class="font-weight-700 float-start text-uppercase mb-4">
                    Create Competition
                  </h5>
                </div>
                {/*  A*/}

                <div className="col-md-12">
                  <div className="row mt-4">
                    <div className="col-md-4">
                      <CompCard
                        classforcard="colorFour"
                        topic="Competition Conducted"
                        headtopic="0"
                      />
                    </div>
                    <div className="col-md-4">
                      <CompCard
                        classforcard="colorFour"
                        topic="Competitions Active"
                        headtopic="0"
                      />
                    </div>
                    <div className="col-md-4">
                      <CompCard
                        classforcard="colorFour"
                        topic="Competition Applicant"
                        headtopic="0"
                      />
                      {/*
                      1. Competition Conducted
                      2. Competitions Active
                      3. Total Applicant
                      4. Total Views 
                      */}
                    </div>
                  </div>
                </div>

                <div className="col-md-12 mt-5 pt-2">
                  <div className="card-competition-list">
                    <CompetitionCardRoundList
                      title="Objective Based Round"
                      link=""
                    />
                    <CompetitionCardRoundList
                      title="Project Based Round"
                      link=""
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default SectionCreateCompetition;

