import React from "react";
import ReactDOM from "react-dom";
import MaterialTable from "material-table";
import { forwardRef } from "react";

import AddBox from "@material-ui/icons/AddBox";
import ArrowDownward from "@material-ui/icons/ArrowDownward";
import Check from "@material-ui/icons/Check";
import ChevronLeft from "@material-ui/icons/ChevronLeft";
import ChevronRight from "@material-ui/icons/ChevronRight";
import Clear from "@material-ui/icons/Clear";
import DeleteOutline from "@material-ui/icons/DeleteOutline";
import Edit from "@material-ui/icons/Edit";
import FilterList from "@material-ui/icons/FilterList";
import FirstPage from "@material-ui/icons/FirstPage";
import LastPage from "@material-ui/icons/LastPage";
import Remove from "@material-ui/icons/Remove";
import SaveAlt from "@material-ui/icons/SaveAlt";
import Search from "@material-ui/icons/Search";
import ViewColumn from "@material-ui/icons/ViewColumn";

const tableIcons = {
  Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
  Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
  Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
  DetailPanel: forwardRef((props, ref) => (
    <ChevronRight {...props} ref={ref} />
  )),
  Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
  Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
  Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
  FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
  LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
  NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  PreviousPage: forwardRef((props, ref) => (
    <ChevronLeft {...props} ref={ref} />
  )),
  ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
  SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
  ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
  ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />),
};

<MaterialTable icons={tableIcons} />;

const DataArray = [
  {
    name: "Ameya Kannurkar",
    applied: "Management Head",
    college: "D. Y. Patil College of Engineering, Pune",
    viewapplication: "view",
  },
  {
    name: "Samyak Meshram",
    applied: "Sales and Marketing Head",
    college: "National Institute of Technology, Raipur",
    viewapplication: "view",
  },
  {
    name: "Krushabh Suryawanshi",
    applied: "Tech and Execution Head",
    college: "D. Y. Patil College of Engineering, Pune",
    viewapplication: "view",
  },
];

const ColoumnArray = [
  { title: "Name", field: "name" },
  {
    title: "Applied for",
    field: "applied",
  },
  { title: "College Name", field: "college" },
  { title: "Application", field: "viewapplication" },
];

const SectionManageCompetition = () => {
  return (
    <>
      <section className="px-5 py-5" id="ManageJob">
        <div className="container" id="job-box">
          <div className="row px-5 py-3">
            <div className="col-md-12">
              <div class="job-bx submit-resume">
                <div class="job-bx-title clearfix">
                  <h5 class="font-weight-700 float-start text-uppercase mb-4">
                    Manage Competition
                  </h5>
                </div>
                {/*  */}
                <table class="table table-striped">
                  <thead>
                    <tr>
                      <div className="form-check">
                        <th scope="col">
                          <input
                            type="checkbox"
                            id="check12"
                            class="form-check-input selectAllCheckBox"
                            name="example1"
                          />
                        </th>
                      </div>
                      <th scope="col">Job Title</th>
                      <th scope="col">Applications</th>
                      <th scope="col">Status</th>
                      <th scope="col">Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <div className="form-check">
                        <th scope="row">
                          <input
                            type="checkbox"
                            class="form-check-input"
                            id="check1"
                            name="example1"
                          />
                        </th>
                      </div>
                      <td>Python Developer</td>
                      <td>37 Applicants</td>
                      <td>Active</td>
                      <td>
                        <div className="job-links d-flex">
                          <a href="http://" className="btn btn-info">
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              width="24"
                              height="24"
                              viewBox="0 0 24 24"
                              fill="none"
                              stroke="currentColor"
                              stroke-width="2"
                              stroke-linecap="round"
                              stroke-linejoin="round"
                              class="feather feather-edit-3 feather-icon"
                            >
                              <path d="M12 20h9"></path>
                              <path d="M16.5 3.5a2.121 2.121 0 0 1 3 3L7 19l-4 1 1-4L16.5 3.5z"></path>
                            </svg>
                          </a>
                          <a href="http://" className="btn btn-danger">
                            i
                          </a>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className="px-5 pb-5" id="ManageJob">
        <div className="container" id="job-box">
          <div className="row px-5 py-3">
            <div className="col-md-12">
              <div class="job-bx submit-resume">
                <div class="job-bx-title clearfix">
                  <h5 class="font-weight-700 float-start text-uppercase mb-4">
                    Competition Participants
                  </h5>
                </div>
                {/*  */}
                <div style={{ maxWidth: "100%" }}>
                  <MaterialTable
                    columns={ColoumnArray}
                    data={DataArray}
                    title=""
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default SectionManageCompetition;

