import React from "react";
import "../PostJob.css";
import { getAuth } from "firebase/auth";
import { getDatabase, ref, set, child, get } from "firebase/database";
const auth = getAuth();
const dbreal = getDatabase();

var dt = new Date().getTime();
var uuid = "xxxx-xxxx-xx".replace(/[xy]/g, function (c) {
  var r = (dt + Math.random() * 16) % 16 | 0;
  dt = Math.floor(dt / 16);
  return (c == "x" ? r : (r & 0x3) | 0x8).toString(16);
});

var dt = new Date();
var mt = dt.getMonth() + 1;
var yr = dt.getYear() - 100;
var today = dt.getDate() + " - " + mt + " - " + yr;

class SectionforTwo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      JobTitle: "",
      JobType: "",
      Company: "",
      minsalary: "",
      maxsalary: "",
      jobCatagory: "",
      location: "",
      JobDescription: "",
      error: {},
    };

    this.FormInput = this.FormInput.bind(this);
    //this.handleSubmit =  this.handleSubmit.bind(this);
  }

  FormInput(event) {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;

    this.setState({ [name]: value });
    console.log(this.state);
  }

  render() {
    const userdet = auth.currentUser;
    const firstError = [];
    var res = userdet.email.split("@");
    var emailUsername = res[0];

    // if not filled CompanyProfile or Candidate Profile Redirect Start
    const dbRef = ref(getDatabase());
    get(
      child(dbRef, "users/" + emailUsername + "/companyprofiledetails/")
    ).then((snapshot) => {
      if (snapshot.exists()) {
        snapshot.forEach(function (data) {
          var val = data.val();
          console.log("Company Profile is Complete");
        });
      } else {
        window.location.href = "/#/home/companyprofile";
      }
    });
    // if not filled CompanyProfile or Candidate Profile Redirect Ends
    // if not filled Profile or Candidate Profile Redirect Start
    get(child(dbRef, "users/" + emailUsername + "/profiledetails/")).then(
      (snapshot) => {
        if (snapshot.exists()) {
          snapshot.forEach(function (data) {
            var val = data.val();
            console.log("User Profile is Complete");
          });
        } else {
          window.location.href = "/#/home/myprofile";
        }
      }
    );
    // if not filled Profile or Candidate Profile Redirect Ends

    const PostJobFormSubmit = (e) => {
      e.preventDefault();

      set(ref(dbreal, "users/" + emailUsername + "/JobPosted/" + uuid + "/"), {
        JobTitle: this.state.JobTitle,
        JobType: this.state.JobType,
        Company: this.state.Company,
        minsalary: this.state.minsalary,
        maxsalary: this.state.maxsalary,
        jobCatagory: this.state.jobCatagory,
        location: this.state.location,
        JobDescription: this.state.JobDescription,
        ApplicantCount: "0",
        JobStatus: "In Review",
        DateToday: today,
      }).catch((error) => {
        console.error(error);
        alert("An Temporary Error Occured");
        window.location.href = "/#/home/dash";
      });

      setTimeout(function () {
        if (firstError.length === 0) {
          alert("Your Job is Posted and is Currently in Review.");
          window.location.href = "/#/home/dash";
        }
      }, 4000);
    };
    return (
      <>
        <section className="p-5">
          <div className="container" id="job-box">
            <div className="row px-5 py-3">
              <div className="col-md-12">
                <div class="job-bx submit-resume">
                  <div class="job-bx-title clearfix">
                    <h5 class="font-weight-700 float-start text-uppercase mb-4">
                      Post A Job
                    </h5>
                  </div>
                  <form onSubmit={PostJobFormSubmit}>
                    <div class="row">
                      <div class="col-lg-12 col-md-12">
                        <div class="form-group">
                          <label>Job Title</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="Enter Job Title"
                            name="JobTitle"
                            value={this.state.JobTitle}
                            onChange={this.FormInput}
                            pattern="[a-zA-Z0-9\s]{3,}"
                            title="Enter Job Title"
                            required
                          />
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Job Type</label>
                          <div class="dropdown bootstrap-select">
                            <select
                              class="form-control"
                              name="JobType"
                              value={this.state.JobType}
                              onChange={this.FormInput}
                              required
                            >
                              <option value="">Job Type</option>
                              <option value="Full Time">Full Time</option>
                              <option value="Part Time">Part Time</option>
                              <option value="Internship">Internship</option>
                              <option value="other">other</option>
                            </select>
                          </div>
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Company</label>
                          <div class="dropdown bootstrap-select">
                            <input
                              type="text"
                              class="form-control"
                              placeholder="Company"
                              name="Company"
                              value={this.state.Company}
                              onChange={this.FormInput}
                              pattern="[a-zA-Z0-9\s]{3,}"
                              title="Enter Company Name"
                              required
                            />
                          </div>
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Minimum Salary ():</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="e.g. 10000"
                            name="minsalary"
                            value={this.state.minsalary}
                            onChange={this.FormInput}
                            pattern="[0-9]{3,}"
                            title="Enter Minimum Salary"
                            required
                          />
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Maximum Salary ():</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="e.g. 20000"
                            name="maxsalary"
                            value={this.state.maxsalary}
                            onChange={this.FormInput}
                            required
                          />
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Job Category</label>
                          <div class="dropdown bootstrap-select">
                            <select
                              class="form-control"
                              name="jobCatagory"
                              value={this.state.jobCatagory}
                              onChange={this.FormInput}
                              required
                            >
                              <option value="">Job Category</option>
                              <option value="office">In-Office</option>
                              <option value="Remote">Remote</option>
                            </select>
                          </div>
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Location</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="London"
                            name="location"
                            value={this.state.location}
                            onChange={this.FormInput}
                            pattern="[A-Za-z]{3,}"
                            title="Enter Location"
                            required
                          />
                        </div>
                      </div>
                      <div class="col-lg-12 col-md-12">
                        <div class="form-group">
                          <label>Description</label>

                          <textarea
                            id="JobDescriptionArea"
                            type="text"
                            class="form-control"
                            placeholder="Describe Job"
                            row="50"
                            name="JobDescription"
                            value={this.state.JobDescription}
                            onChange={this.FormInput}
                            pattern="[A-Za-z]{3,}"
                            title="Enter Job Description"
                            required
                          ></textarea>
                        </div>
                      </div>
                    </div>
                    <button
                      type="submit"
                      class="site-button btn btn-primary m-b30 px-5"
                    >
                      Make Post
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default SectionforTwo;
// export { JobPostInputSection };
