import React, { Component } from "react";
import Login from "../Login";
import fire from "../config/fire";
import DashOne from "../Components/DashOne";
import {
  getAuth,
  createUserWithEmailAndPassword,
  onAuthStateChanged,
} from "firebase/auth";

const auth = getAuth();
class HomeDashR extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: null,
    };
    this.authListener = this.authListener.bind(this);
  }

  componentDidMount() {
    this.authListener();
  }
  authListener() {
    onAuthStateChanged(auth, (user) => {
      if (user) {
        this.setState({ user });
      } else {
        this.setState({ user: null });
      }
    });
  }

  render() {
    return (
      <>
        {/* {(() => {
          if (this.state.user) {
            if (this.state.user.emailVerified == true) {
              return window.location.replace("/#/dash");
            }
          }
        })()} */}
        {/* <Login /> */}
        <section id="Dash_Page">
          {this.state.user ? <DashOne /> : <Login />}
        </section>
      </>
    );
  }
}

export default HomeDashR;
