import React from "react";
import "../ManageJob.css";
import { getAuth } from "firebase/auth";
import { get, child, getDatabase, ref, onValue } from "firebase/database";
import { data } from "jquery";
const db = getDatabase();
const auth = getAuth();

// const SectionforThree = () => {
class SectionforThree extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: [],
    };
  }

  render() {
    const userdet = auth.currentUser;
    var res = userdet.email.split("@");
    var emailUsername = res[0];
    const dblocation = "users/" + emailUsername + "/JobPosted/";

    const dbRef = ref(getDatabase());

    get(child(dbRef, dblocation))
      .then((snapshot) => {
        if (snapshot.exists()) {
          var content = "";
          snapshot.forEach(function (data) {
            var val = data.val();

            content +=
              '<tr><div className="form-check"><th scope="row"><input type="checkbox" className="form-check-input"id="check1" name="example1" /> </th> </div>';
            content += "<td>";
            content += val.JobTitle;
            content += "</td>";
            content += "<td>";
            content += val.ApplicantCount;
            content += " Applicants </td>";
            content += "<td>";
            content += val.DateToday;
            content += " </td>";
            content += "<td>";
            content += val.JobStatus;
            content += "</td>";
            content +=
              '<td> <div className="job-links d-flex"> <a href="http://" className="btn btn-info"> <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-edit-3 feather-icon" > <path d="M12 20h9"></path> <path d="M16.5 3.5a2.121 2.121 0 0 1 3 3L7 19l-4 1 1-4L16.5 3.5z"></path> </svg> </a> <a href="http://" className="btn btn-danger"> i </a> </div> </td> </tr>';
          });
          // console.log(snapshot.val());
          document.getElementById("tablearea").innerHTML = content;
        } else {
          console.log("No data available");
          document.getElementById("tablearea").innerHTML = "No Job Posted";
        }
      })
      .catch((error) => {
        console.error(error);
        document.getElementById("tablearea").innerHTML = "No Job Posted";
      });

    return (
      <>
        {/* {onValue(JobPosts, (snapshot) => {
          const data = snapshot.val();
          console.log(data);
        })} */}
        <section className="p-5" id="ManageJob">
          <div className="container" id="job-box">
            <div className="row px-5 py-3">
              <div className="col-md-12">
                <div className="job-bx submit-resume">
                  <div className="job-bx-title clearfix">
                    <h5 className="font-weight-700 float-start text-uppercase mb-4">
                      Manage Jobs
                    </h5>
                  </div>
                  <table className="table table-striped">
                    <thead>
                      <tr>
                        <div className="form-check">
                          <th scope="col">
                            <input
                              type="checkbox"
                              id="check12"
                              className="form-check-input selectAllCheckBox"
                              name="example1"
                            />
                          </th>
                        </div>
                        <th scope="col">Job Title</th>
                        <th scope="col">Applications</th>
                        <th scope="col">Posted On</th>
                        <th scope="col">Status</th>
                        <th scope="col">Action</th>
                      </tr>
                    </thead>
                    <tbody id="tablearea">
                      {/*<tr>
                         <div className="form-check">
                          <th scope="row">
                            <input
                              type="checkbox"
                              className="form-check-input"
                              id="check1"
                              name="example1"
                            />
                          </th>
                        </div>
                        <td>Python Developer</td>
                        <td>37 Applicants</td>
                        <td>Active</td>
                        <td>
                          <div className="job-links d-flex">
                            <a href="http://" className="btn btn-info">
                              <svg
                                xmlns="http://www.w3.org/2000/svg"
                                width="24"
                                height="24"
                                viewBox="0 0 24 24"
                                fill="none"
                                stroke="currentColor"
                                strokeWidth="2"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                className="feather feather-edit-3 feather-icon"
                              >
                                <path d="M12 20h9"></path>
                                <path d="M16.5 3.5a2.121 2.121 0 0 1 3 3L7 19l-4 1 1-4L16.5 3.5z"></path>
                              </svg>
                            </a>
                            <a href="http://" className="btn btn-danger">
                              i
                            </a>
                          </div>
                        </td> 
                        
                      </tr>*/}
                      {/* <span id="tablearea"></span> */}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default SectionforThree;
