class SectionCompanyProfile extends React.Component {
  //const SectionCompanyProfile = () => {
  constructor(props) {
    super(props);
    this.state = {
      logo: "",
      CompanyName: "",
      CompanyCategory: "",
      EmployeeSize: "",
      CompanyType: "",
      MonthFounded: "",
      YearFounded: "",
      Email: "",
      WebLink: "",
      CompanyDesc: "",
      Country: "",
      HQPhone: "",
      CompanyAddress: "",
      CityName: "Pune",
      State: "",
      Zip: "",
      error: {},
    };
    this.FormInput = this.FormInput.bind(this);
  }

  FormInput(event) {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;
    this.setState({ [name]: value });
  }
  render() {
    const userdet = auth.currentUser;
    const firstError = [];

    const CompanyProfileUpdate = (e) => {
      e.preventDefault();
      console.log("Form Submitted");
      var res = userdet.email.split("@");
      var emailUsername = res[0];
      set(ref(dbreal, "users/" + emailUsername + "/companyprofile/"), {
        //logo: this.state.fName,
        CompanyName: this.state.CompanyName,
        CompanyCategory: this.state.CompanyCategory,
        EmployeeSize: this.state.EmployeeSize,
        CompanyType: this.state.CompanyType,
        MonthFounded: this.state.MonthFounded,
        YearFounded: this.state.YearFounded,
        Email: this.state.Email,
        WebLink: this.state.WebLink,
        CompanyDesc: this.state.CompanyDesc,
        Country: this.state.Country,
        HQPhone: this.state.HQPhone,
        CompanyAddress: this.state.CompanyAddress,
        CityName: this.state.CityName,
        State: this.state.State,
        Zip: this.state.Zip,
      }).catch((error) => {
        console.error(error);
        firstError.push("error");
        alert("An Temporary Error Occured");
        //window.location.href = "/#/home/dash";
      });
      console.log("Form My Profile");

      setTimeout(function () {
        if (firstError.length === 0) {
          alert("Your Profile is Updated");
          set(
            ref(dbreal, "users/" + emailUsername + "/companyprofiledetails/"),
            {
              isSubmitted: "true",
            }
          );
          window.location.href = "/#/home/dash";
        }
      }, 8000);
    };

    return (
      <>
        <section className="p-5" id="ManageJob">
          <div className="container" id="job-box">
            <div className="row px-5 py-3">
              <div className="col-md-12">
                <div class="job-bx submit-resume">
                  <div class="job-bx-title clearfix">
                    <h5 class="font-weight-700 float-start text-uppercase mb-4">
                      Company Profile
                    </h5>
                  </div>
                  {/*  */}
                </div>
                <div className="col-md-12">
                  <form onSubmit={CompanyProfileUpdate}>
                    <div class="row">
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Upload Logo</label>
                          <input
                            type="file"
                            class="form-control"
                            placeholder="Logo"
                            name="logo"
                            value={this.state.logo}
                            onChange={this.FormInput}
                            required
                          />
                        </div>
                      </div>

                      <div class="col-lg-12 col-md-12">
                        <div class="form-group">
                          <label>Company Name</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="Enter Company Name"
                            name="CompanyName"
                            value={this.state.CompanyName}
                            onChange={this.FormInput}
                            pattern="[a-zA-Z0-9\s]{3,}"
                            title="Enter Company Name"
                            required
                          />
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Company Category</label>
                          <div class="dropdown bootstrap-select">
                            <select
                              class="form-control"
                              name="CompanyCategory"
                              value={this.state.CompanyCategory}
                              onChange={this.FormInput}
                              required
                            >
                              <CompanyCategoryOptionList />
                            </select>
                          </div>
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Employee Size</label>
                          <div class="dropdown bootstrap-select">
                            <select
                              class="form-control"
                              name="EmployeeSize"
                              value={this.state.EmployeeSize}
                              onChange={this.FormInput}
                              required
                            >
                              <option value="">Employee Size</option>
                              <option value="0-10">0-10</option>
                              <option value="11-100">11-100</option>
                              <option value="101-500">101-500</option>
                              <option value="501-1000">501-1000</option>
                              <option value="1001-10000">1001-10000</option>
                              <option value="More Than 10000">10000+</option>
                            </select>
                          </div>
                        </div>
                      </div>

                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Company Type</label>
                          <div class="dropdown bootstrap-select">
                            <select
                              class="form-control"
                              name="CompanyType"
                              value={this.state.CompanyType}
                              onChange={this.FormInput}
                              required
                            >
                              <option value="">Company Type</option>
                              <option value="EDUCATIONAL">Educational</option>
                              <option value="GOVERNMENT_AGENCY">
                                Government Agency
                              </option>
                              <option value="NON_PROFIT">Non Profit</option>
                              <option value="PARTNERSHIP">Partnership</option>
                              <option value="PRIVATELY_HELD">
                                Privately Held
                              </option>
                              <option value="PUBLIC_COMPANY">
                                Public Company
                              </option>
                              <option value="SELF_EMPLOYED">
                                Self Employed
                              </option>
                              <option value="SELF_OWNED">Self Owned</option>
                              <option value="Recruitment_Service">
                                Recruitment Service
                              </option>
                            </select>
                          </div>
                        </div>
                      </div>

                      <div class="col-lg-3 col-md-3">
                        <div class="form-group">
                          <label>Month Founded</label>
                          <select
                            class="form-control"
                            name="MonthFounded"
                            value={this.state.MonthFounded}
                            onChange={this.FormInput}
                            required
                          >
                            <option value="">Month</option>
                            <option value="Jan">Jan</option>
                            <option value="Feb">Feb</option>
                            <option value="Mar">Mar</option>
                            <option value="Apr">Apr</option>
                            <option value="May">May</option>
                            <option value="Jun">Jun</option>
                            <option value="Jul">Jul</option>
                            <option value="Aug">Aug</option>
                            <option value="Sep">Sep</option>
                            <option value="Oct">Oct</option>
                            <option value="Nov">Nov</option>
                            <option value="Dec">Dec</option>
                          </select>
                        </div>
                      </div>
                      <div class="col-lg-3 col-md-3">
                        <div class="form-group">
                          <label>Year Founded</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="ex. 2020"
                            name="YearFounded"
                            value={this.state.YearFounded}
                            onChange={this.FormInput}
                            pattern="[0-9]{4}"
                            title="Enter Year Founded"
                            required
                          />
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Email</label>
                          <input
                            type="email"
                            class="form-control"
                            placeholder="Email"
                            name="Email"
                            value={this.state.Email}
                            onChange={this.FormInput}
                            pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$"
                            title="Enter Company Email"
                            required
                          />
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Website Link</label>
                          <input
                            type="url"
                            class="form-control"
                            placeholder="https://"
                            name="WebLink"
                            value={this.state.WebLink}
                            onChange={this.FormInput}
                            pattern="[Hh][Tt][Tt][Pp][Ss]?:\/\/(?:(?:[a-zA-Z\u00a1-\uffff0-9]+-?)*[a-zA-Z\u00a1-\uffff0-9]+)(?:\.(?:[a-zA-Z\u00a1-\uffff0-9]+-?)*[a-zA-Z\u00a1-\uffff0-9]+)*(?:\.(?:[a-zA-Z\u00a1-\uffff]{2,}))(?::\d{2,5})?(?:\/[^\s]*)?"
                            title="Enter Website Link"
                            required
                          />
                        </div>
                      </div>
                      <div class="col-lg-12 col-md-12">
                        <div class="form-group">
                          <label>Company Description</label>
                          <textarea
                            type="text"
                            class="form-control"
                            placeholder="Describe Your Company"
                            name="CompanyDesc"
                            value={this.state.CompanyDesc}
                            onChange={this.FormInput}
                            pattern="[A-Za-z]{20,}"
                            title="Enter Company Description"
                            required
                          ></textarea>
                        </div>
                      </div>
                      <div className="col-lg-12">
                        <div class="job-bx-title clearfix">
                          <h5 class="font-weight-700 float-start text-uppercase mb-4">
                            Contact Information
                          </h5>
                        </div>
                      </div>

                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Country</label>
                          <select
                            class="form-control"
                            name="Country"
                            value={this.state.Country}
                            onChange={this.FormInput}
                            required
                          >
                            <option value="">Country</option>
                            <option value="India">India (+91)</option>
                            <option value="other">other</option>
                          </select>
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Phone</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="20 xxxxxxx"
                            name="HQPhone"
                            value={this.state.HQPhone}
                            onChange={this.FormInput}
                            pattern="[0-9]{10}"
                            title="Enter Head Quater Phone"
                            required
                          />
                        </div>
                      </div>
                      <div class="col-lg-8 col-md-8">
                        <div class="form-group">
                          <label>Address</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="Company Private Limited, Lane no. 8, Delhi"
                            name="CompanyAddress"
                            value={this.state.CompanyAddress}
                            onChange={this.FormInput}
                            pattern="[A-Za-z]{9,}"
                            title="Enter Company Address"
                            required
                          />
                        </div>
                      </div>
                      <div class="col-lg-4 col-md-4">
                        <div class="form-group">
                          <label>City</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="Pune, Delhi, Bangalore, etc."
                            name="CityName"
                            value={this.state.CityName}
                            onChange={this.FormInput}
                            pattern="[A-Za-z]{3,}"
                            title="Enter City"
                            required
                          />
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>State</label>
                          <select
                            class="form-control"
                            name="State"
                            value={this.state.State}
                            onChange={this.FormInput}
                            required
                          >
                            <StateOptionList />
                          </select>
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Zip Code</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="110 011"
                            name="Zip"
                            value={this.state.Zip}
                            onChange={this.FormInput}
                            pattern="[0-9]{6}"
                            title="Enter Zip Code"
                            required
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-12">
                      <div className="form-group">
                        <div class="mapouter">
                          <div class="gmap_canvas">
                            <iframe
                              width="1000"
                              height="300"
                              id="gmap_canvas"
                              src={
                                "https://maps.google.com/maps?q=" +
                                this.state.CityName +
                                "&amp;t=&amp;z=7&amp;ie=UTF8&amp;iwloc=&amp;output=embed"
                              }
                              frameborder="0"
                              scrolling="no"
                              marginheight="0"
                              marginwidth="0"
                            ></iframe>
                            <br />
                          </div>
                        </div>
                      </div>
                    </div>
                    <button
                      type="submit"
                      class="site-button btn btn-primary m-b30 px-5"
                    >
                      Save
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default SectionCompanyProfile;

