import React from "react";
import "../ManageJob.css";
import { getAuth } from "firebase/auth";
import { getDatabase, ref, set, child, get } from "firebase/database";
const auth = getAuth();
const dbreal = getDatabase();

class SectionMyProfile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fName: "",
      lName: "",
      CompanyName: "",
      JobType: "",
      CurrentRole: "",
      phone: "",
      error: {},
    };
    this.FormInput = this.FormInput.bind(this);
  }

  FormInput(event) {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;
    this.setState({ [name]: value });
  }

  render() {
    const userdet = auth.currentUser;
    this.state.email = userdet.email;
    const firstError = [];

    const ProfileUpdate = (e) => {
      e.preventDefault();
      console.log("Form Submitted");
      var res = userdet.email.split("@");
      var emailUsername = res[0];
      set(ref(dbreal, "users/" + emailUsername + "/profile/"), {
        fName: this.state.fName,
        lName: this.state.lName,
        CompanyName: this.state.CompanyName,
        JobType: this.state.JobType,
        CurrentRole: this.state.CurrentRole,
        email: this.state.email,
        phone: this.state.phone,
      }).catch((error) => {
        console.error(error);
        firstError.push("error");
        alert("An Temporary Error Occured");
        //window.location.href = "/#/home/dash";
      });
      console.log("Form My Profile");

      setTimeout(function () {
        if (firstError.length === 0) {
          alert("Your Profile is Updated");
          set(ref(dbreal, "users/" + emailUsername + "/profiledetails/"), {
            isSubmitted: "true",
          });
          window.location.href = "/#/home/dash";
        }
      }, 8000);
    };

    const ProfileDataLoad = () => {
      const dbRef = ref(getDatabase());
      var res = userdet.email.split("@");
      var emailUsername = res[0];

      return (
        <>
          {console.log("E")}
          {(() => {
            get(child(dbRef, "users/" + emailUsername + "/profiledetails/"))
              .then((snapshot) => {
                if (snapshot.exists()) {
                  console.log(snapshot.val());
                } else {
                  console.log("No data available");
                  alert("Make Sure You are in Stable Internet Connection");
                }
              })
              .catch((error) => {
                console.error(error);
                alert("Make Sure You are in Stable Internet Connection");
              });
          })()}
        </>
      );
      // return <></>;
    };

    return (
      <>
        <section className="p-5" id="ManageJob" onLoad={ProfileDataLoad}>
          <div className="container" id="job-box">
            <div className="row px-5 py-3">
              <div className="col-md-12">
                <div class="job-bx submit-resume">
                  <div class="job-bx-title clearfix">
                    <h5 class="font-weight-700 float-start text-uppercase mb-4">
                      My Profile
                    </h5>
                  </div>
                  {/*  */}
                </div>
                <div className="col-md-12">
                  <form onSubmit={ProfileUpdate}>
                    <div class="row">
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>First Name</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="First Name"
                            name="fName"
                            value={this.state.fName}
                            onChange={this.FormInput}
                            pattern="[A-Za-z]{3,}"
                            required
                            title="Enter First Name"
                          />
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Last Name</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="Last Name"
                            name="lName"
                            value={this.state.lName}
                            onChange={this.FormInput}
                            pattern="[A-Za-z]{3,}"
                            required
                            title="Enter Last Name"
                          />
                        </div>
                      </div>
                      <div class="col-lg-12 col-md-12">
                        <div class="form-group">
                          <label>Company Name</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="Enter Company Name"
                            name="CompanyName"
                            value={this.state.CompanyName}
                            onChange={this.FormInput}
                            pattern="[a-zA-Z0-9\s]{3,}"
                            required
                            title="Enter Company Name"
                          />
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Job Type</label>
                          <div class="dropdown bootstrap-select">
                            <select
                              class="form-control"
                              name="JobType"
                              value={this.state.JobType}
                              onChange={this.FormInput}
                              required
                            >
                              <option value="">Job Type</option>
                              <option value="Full Time">Full Time</option>
                              <option value="Part Time">Part Time</option>
                              <option value="Internship">Internship</option>
                              <option value="Freelance">Freelance</option>
                            </select>
                          </div>
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Current Role</label>
                          <div class="dropdown bootstrap-select">
                            <input
                              type="text"
                              class="form-control"
                              placeholder="Director, Founder, Human Resource Executive, etc."
                              name="CurrentRole"
                              value={this.state.CurrentRole}
                              onChange={this.FormInput}
                              pattern="[a-zA-Z0-9\s]{3,}"
                              required
                              title="Enter Current Role in Company (Example: Director, Founder, Human Resource Executive, etc.)"
                            />
                          </div>
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Email</label>
                          <input
                            type="email"
                            class="form-control"
                            placeholder="Email"
                            name="email"
                            value={this.state.email}
                            onChange={this.FormInput}
                            disabled
                          />
                        </div>
                      </div>
                      <div class="col-lg-6 col-md-6">
                        <div class="form-group">
                          <label>Phone</label>
                          <input
                            type="text"
                            class="form-control"
                            placeholder="+91 xxxxxxxxx"
                            name="phone"
                            value={this.state.phone}
                            onChange={this.FormInput}
                            pattern="[0-9]{10}"
                            required
                            title="Enter 10 Digit Phone Number"
                          />
                        </div>
                      </div>
                    </div>
                    <button
                      type="submit"
                      class="site-button btn btn-primary m-b30 px-5"
                    >
                      Save
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default SectionMyProfile;

