import React from "react";
import Sidenav from "./Sidenav";
import Sidebar from "./Sidebar";
import SectionforOne from "./Pages/SectionforOne";
import SectionforTwo from "./Pages/SectionforTwo";
import { useParams } from "react-router-dom";
import SectionforThree from "./Pages/SectionforThree";
import SectionManageApp from "./Pages/SectionManageApp";
import SectionCreateCompetition from "./Pages/SectionCreateCompetition";
import SectionManageCompetition from "./Pages/SectionManageCompetition";
import SectionMyProfile from "./Pages/SectionMyProfile";
import SectionCompanyProfile from "./Pages/SectionCompanyProfile";

const DashOne = (props) => {
  const { page } = useParams();
  return (
    <>
      <div className="row">
        <div className="col-md-3 px-0">
          <Sidebar />
        </div>

        <div className="col-md-9 px-0">
          <Sidenav />
          {(() => {
            if (page === "dash") {
              return (
                <>
                  <SectionforOne />
                </>
              );
            } else if (page === "post") {
              return (
                <>
                  <SectionforTwo />
                </>
              );
            } else if (page === "managepost") {
              return (
                <>
                  <SectionforThree />
                </>
              );
            } else if (page === "manageapp") {
              return (
                <>
                  <SectionManageApp />
                </>
              );
            } else if (page === "createcompetition") {
              return (
                <>
                  <SectionCreateCompetition />
                </>
              );
            } else if (page === "managecompetition") {
              return (
                <>
                  <SectionManageCompetition />
                </>
              );
            } else if (page === "myprofile") {
              return (
                <>
                  <SectionMyProfile />
                </>
              );
            } else if (page === "companyprofile") {
              return (
                <>
                  <SectionCompanyProfile />
                </>
              );
            } //  else if (page === "myprofile") {
          })()}
        </div>
      </div>
    </>
  );
};

export default DashOne;
